import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { MenuController, ModalController } from '@ionic/angular';
import * as moment from 'moment';
import { AsignacionnettsegurComponent } from 'src/app/components/asignacionnettsegur/asignacionnettsegur.component';
import { ApiService } from 'src/app/Services/Api/Api.service';

@Component({
  selector: 'app-revision',
  templateUrl: './revision.page.html',
  styleUrls: ['./revision.page.scss'],
})
export class RevisionPage implements OnInit {

  activitiesList = [];
  load = false;
  tkn;
  txt = '';
  temp = []


  constructor(
    private menuCtrl: MenuController,
    private api: ApiService,
    private modalCtrl: ModalController,
    private route: ActivatedRoute
  ) { }

  ngOnInit() {
  }

  ionViewWillEnter() {
     this.menuCtrl.enable(false, 'menu')
    this.route.queryParams.subscribe((param) => {
      console.log(param);
      this.tkn = param.accesstoken;
      this.getActivities()
    })
  
   
   
  }


  getData(guid) {
    return new Promise((resolve, reject) => {
      this.api.getActivitieInfo(this.tkn, guid).subscribe((data: any) => {
        console.log(data, 'DATA')
        resolve(data);
      })
    }) 
  }

  async getActivities() {

    this.api.getActivities2(this.tkn, moment().subtract(3, 'months').format('YYYY-MM-DD HH:mm') , moment().format('YYYY-MM-DD HH:mm'), 'HWWbijhSJH').subscribe(async (data: any) => {
    console.log(data);

    //data = data.filter((it) => it.CompanyStatusName == 'CREADO')

    if (!data) {
      this.load = true;
      return;
    }

    if (data.length == 0) {
      this.load = true;
      return;
    }
    

    for (const item of data) {
        const response: any = await this.getData(item.GUID)
        if (response) {
          this.activitiesList.push(response);
        }

        this.load = true;
    }

    this.temp = this.activitiesList;

    console.log(this.activitiesList)
    
  }, (err) =>  this.load = true )

}


}
